'use server';

import { generateTradingSignal } from '@/ai/flows/generate-trading-signal';
import { getTicker, getOrderBook, getRecentTrades, getKline } from '@/lib/bybit-api';

export async function getAiTradingSignal(symbol: string, timeframe: string) {
  try {
    const result = await generateTradingSignal({ symbol, timeframe });
    return { success: true, analysis: result.analysis };
  } catch (error) {
    console.error('Error generating AI trading signal:', error);
    return { success: false, error: 'Failed to generate AI signal. Please try again.' };
  }
}

export async function getMarketData(symbol: string) {
    return getTicker(symbol);
}

export async function getOrderBookData(symbol: string) {
    return getOrderBook(symbol);
}

export async function getRecentTradesData(symbol: string) {
    return getRecentTrades(symbol);
}

export async function getKlineData(symbol: string, interval: string) {
    return getKline(symbol, interval);
}
