'use client';

import { useState, useEffect } from 'react';
import { Card, CardHeader, CardTitle, CardContent } from '@/components/ui/card';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table';
import { getRecentTradesData } from '@/lib/actions';
import type { RecentTrade } from '@/lib/bybit-api';
import { ScrollArea } from '@/components/ui/scroll-area';
import { Skeleton } from '@/components/ui/skeleton';
import { format } from 'date-fns';

interface RecentTradesProps {
  symbol: string;
}

export function RecentTrades({ symbol }: RecentTradesProps) {
  const [trades, setTrades] = useState<RecentTrade[] | null>(null);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    const fetchData = async () => {
      setIsLoading(true);
      const recentTrades = await getRecentTradesData(symbol);
      setTrades(recentTrades);
      setIsLoading(false);
    };

    fetchData();
  }, [symbol]);

  const formatTime = (timestamp: number) => {
    return format(new Date(timestamp), 'HH:mm:ss');
  }

  const LoadingRows = () => (
    Array.from({ length: 15 }).map((_, i) => (
        <TableRow key={i} className="text-xs">
            <TableCell className="p-2"><Skeleton className="h-4 w-full" /></TableCell>
            <TableCell className="p-2"><Skeleton className="h-4 w-full" /></TableCell>
            <TableCell className="p-2"><Skeleton className="h-4 w-full" /></TableCell>
        </TableRow>
    ))
  );

  return (
    <Card>
      <CardHeader>
        <CardTitle>Recent Trades</CardTitle>
      </CardHeader>
      <CardContent className="p-0">
        <ScrollArea className="h-[240px]">
          <Table>
            <TableHeader>
              <TableRow className="text-xs">
                <TableHead className="p-2">Time</TableHead>
                <TableHead className="p-2">Price (USDT)</TableHead>
                <TableHead className="p-2 text-right">Amount</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {isLoading && <LoadingRows />}
              {!isLoading && trades?.map((trade, index) => (
                <TableRow key={index} className="text-xs font-mono">
                  <TableCell className="p-2">{formatTime(trade.execTime)}</TableCell>
                  <TableCell className={`p-2 ${trade.side === 'Buy' ? 'text-green-500' : 'text-red-500'}`}>{parseFloat(trade.price).toFixed(2)}</TableCell>
                  <TableCell className="p-2 text-right">{parseFloat(trade.qty).toFixed(3)}</TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </ScrollArea>
      </CardContent>
    </Card>
  );
}
